/* MOBILE LARGE */

@media (max-width: 481px) {
  /* HOME */
  .home-container {
    width: 90vw;
  }

  .cmk_full img {
    margin: 1em 0;
    max-height: 8em;
  }

  .sideways {
    display: inline;
    display: initial;
  }
  .extra-links {
    display: inline;
    display: initial;
    /* flex-direction: ;
    justify-content: space-between; */
  }
  .extra-images img { 
    height: auto;
    width: 90vw;
    margin: 1.5em 0;
  }
  figure {
    margin: 0;
  }
  .fig-bottom {
    margin-bottom: 1.5em;
  }
  /* ABOUT */
  .about {
    width: auto;
    width: initial;
  }  
  .about ul {
    padding: 0;
  }

  /* SECTION */
  .side-nav-container {
    display: none;
  }
  .dropdown-content {
    width: auto;
    width: initial;
  }

  /* POLICIES */
  .policies-container {
    width: auto;
    width: initial;
  }
  /* CONTACT */
  .m-container {
    min-height: 0;
    min-height: initial;
  }
}

/* LAPTOP */

@media (max-width: 1441px) {
  .navbar {
    font-size: .8em;
  }
}
